export default {
	props:["nav"],
	data() {
		return {
			statusBarHeight: 0, 
			titleBarHeight: 0, 
		}
	},
	created() {
		var  that = this;
		 	uni.getSystemInfo({
		 		success: function(res) {
		 			if (res.model.indexOf('iPhone') !== -1) {
						that.titleBarHeight = 43 + 'px';
		 			} else {
		 				that.titleBarHeight = 48  + 'px';
					}
						that.statusBarHeight = res.statusBarHeight  + 'px'
				},
					
		 	})
	 }
}
